.site-navigation__item {
  list-style: none;
}

.site-navigation {
  width: 320px;
  padding: 0;
  margin: 0;
}

.site-navigation__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.site-navigation__item {
  margin: 0;
  padding: 0;
  background-color: #283645;
  border-top: 1px solid #485461;
  border-bottom: 1px solid black;
  display: flex;
}

.site-navigation__link {
  font-family: "Open Sans", Arial, sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  margin: 0;
  padding: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  width: 100%;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
}

.site-navigation__container {
  width: 280px;
  margin: 0 auto;
  padding: 0 20px;
  min-height: 65px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.site-navigation__logo {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.site-navigation__logo-image {
  &:hover g {
    fill: #d22856;
  }
  &:active g {
    fill: rgba(255, 255, 255, 0.3);
  }
}

.site-navigation__toggle {
  min-width: 52px;
  min-height: 27px;
  border: none;
  background-color: rgba(0, 0, 0, 0);
}

.site-navigation__svg {
    path {
  fill: white;
  }
  &:hover path {
    fill: #d22856;
  }
  &:active path {
    fill: rgba(210, 40, 86, 0.3);
  }
}

@media (min-width: 660px) {
  .site-navigation {
    width: 660px;
    margin: 0;
    padding: 0;
  }
  .site-navigation__container {
    width: 620px;
  }
}

@media (min-width: 1200px) {

  .site-navigation {
    width: 1200px;
    position: absolute;
    z-index: 3;
    right: -300px;
  }

  .site-navigation__list {
    flex-direction: row;
    justify-content: space-between;
    width: 620px;
    min-height: 110px;
  }

  .site-navigation__item {
    flex-grow: 1;
    align-items: center;
    border: none;
    background-color: rgba(0, 0, 0, 0);
  }

  .site-navigation__item:last-child {
    display: none;
  }

  .site-navigation__link {
    font-weight: 100;
  }

}
